<template lang="html">
  <div class="question-container">

  <b-jumbotron>

    <template slot="lead">
      {{currentQuestions.question}}
    </template>

    <hr class="my-4" />

    <b-list-group>
        <b-list-group-item
        v-for="(answer,index) in answers"
        :key="index"
        
        >
        {{ answer }}
        </b-list-group-item>
    </b-list-group>

    <b-button @click="previous" variant="success">Previous</b-button>
    <b-button variant="primary" >Submit</b-button>
    <b-button @click="next" variant="success">Next</b-button>
  </b-jumbotron>

  </div>
</template>

<script>
export default {
  props: {
    currentQuestions: Object,
    next: Function,
    previous: Function
  },
  computed: {
    answers(){
      let answers=[...this.currentQuestions.incorrect_answers]
      answers.push(this.currentQuestions.correct_answer)
      return answers
    }
  },
  mounted(){
    console.log(this.currentQuestions.incorrect_answers)
  }
}
</script scoped>

<style lang="css" scoped>
.list-group{
  margin-bottom:15px;
}
.btn{
  margin:0px 15px;
}
</style>
