<template>
  <div id="app">
    <Header/>
    <b-container class="bv-example-row">
  <b-row>
    <b-col sm="6" offset="3">
    <Quiz
      v-if="questions.length"
      :currentQuestions="questions[index]"
      :previous="previous"
      :next="next"
      />
    </b-col>
  </b-row>
</b-container>

  </div>
</template>

<script>
import Header from './components/Header.vue'
import Quiz from './components/Quiz.vue'

export default {
  name: 'app',
  components: {
    Header,
    Quiz
  },

  data() {
    return{
      questions:[],
      index:0
    }
  },
  methods: {
    next() {
     this.index++
   },
    previous() {
      this.index--
    }
  },
  mounted: function() {
    fetch('https://opentdb.com/api.php?amount=10&category=21&type=multiple',{
      method:'get'
    })
      .then((response) => {
        return response.json()
      })
      .then((jsonData) => {
         this.questions = jsonData.results
      })
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
